*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}



body {
  font-family:'montseratt', sans-serif;
}

h3  {
  text-align: center;
  margin-bottom: 10px;
  color: white;
  font-size: 30px;
}

.App{
  background:url("./assets/cold.jpg");
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
  transition: .4s ease-in-out;
}

.App-warm{
  background-image: url("./assets/warm-bg.jpg");
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
  transition: .4ease-in-out;
}

main{
  min-height: 100vh;
  background-image: linear-gradient(rgba(0,0,0,.5),rgba(0,0,0,.75));
  padding:25px;
}

.search-box{
  width: 100%;
  margin:0 0 75px;
}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border:none;
  outline:none;

  background-color:rgba(255,255,255,.5);
  border-radius: 10px 10px 10px 10px;
  /* margin-top: -25px; */

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color:#313131;
  font-size: 20px;

  transition: .4s ease;
}

.search-box .search-bar:focus{
  background-color: rgba(255,255,255,.8);
}

.location-box .location{
  color:#fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,70,.5);
}

.location-box .date{
  color:#fff;
  font-size: 20px;
  font-weight: 300;
  text-align: center;
  font-style: italic;
  text-shadow: 2px 2px rgba(50,50,70,.5);
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display:inline-block;
  margin:30px auto;
  background-color: rgba(255,255,255,.2);
  border-radius: 16px;

  padding:15px 25px;

  color:#fff;
  font-size: 100px;
  font-weight: 900;
  
  text-shadow:3px 6px rgba(255,255,255,.5);
  text-align: center;
  box-shadow:3px 6px rgba(0,0,0,.2);
}


.weather-box .weather{
  color:#fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,.5);
}